.container-form {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 60vh;
    background-color: #f0f4f8;

    padding-top: 6rem;
}   


.form {
    line-height: 1.5;
         
    margin: 2rem auto;
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: 758px;
	background-color: #FFF;
	border-radius: 16px;
	box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1), 0 4px 10px rgba(0, 0, 0, 0.05);
	padding: 2.5rem;
	transition: box-shadow 0.3s ease;

    img {
	    max-width: 100%;
	    display: block;
    }

    textarea {
        resize: none;
        height: 134px;
    }


    .card-image {
	border-radius: 12px;
	overflow: hidden;
	padding-bottom: 33%;
	background-image: url('https://assets.codepen.io/285131/coffee_1.jpg');
	background-repeat: no-repeat;
	background-size: 130%;
    background-position: -6px 39%;
	position: relative;
	box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);

    .card-heading {
	position: absolute;
	left: 10%;
	top: 15%;
	right: 10%;
	font-size: 1.75rem;
	font-weight: 700;
	color: #735400;
	line-height: 1.222;
	text-shadow: 0 1px 2px rgba(255, 255, 255, 0.3);
	small {
		display: block;
		font-size: 1.75em;
		font-weight: 300;
		margin-top: .25em;
	}
}
}
}

.card-form {
	padding: 2rem 1rem 0;

    .input {
	display: flex;
	flex-direction: column-reverse;
	position: relative;
	padding-top: 1.5rem;
	&+.input {
		margin-top: 1.5rem;
	}
}

}

.input-label {
	color: #8597a3;
	position: absolute;
	top: 1.5rem;
	transition: .25s ease;
}

.input-field {
	border: 0;
    color: #111111 !important;
	z-index: 1;
	background-color: transparent;
	border-bottom: 2px solid #eee; 
	font: inherit;
	font-size: 1.625rem;
	padding: 1.25rem 0;
	&:focus, &:valid {
		outline: 0;
		border-bottom-color: #6658d3;
		&+.input-label {
			color: #6658d3;
			transform: translateY(-1.5rem);
		}
	}
}

.action {
	margin-top: 2rem;
}

.action-button {
	font: inherit;
	font-size: 1.55rem;
	padding: 1.2em;
	width: 100%;
	font-weight: 600;
	background: linear-gradient(135deg, #6658d3 0%, #8b7ff0 100%);
	border-radius: 8px;
	color: #FFF;
	border: 0;
	cursor: pointer;
	transition: all 0.3s ease;
	box-shadow: 0 4px 15px rgba(102, 88, 211, 0.3);
	&:hover {
		transform: translateY(-2px);
		box-shadow: 0 6px 20px rgba(102, 88, 211, 0.4);
	}
	&:active {
		transform: translateY(0);
	}
	&:focus {
		outline: 0;
	}
}

.card-info {
	padding: 1rem 1rem;
	text-align: center;
	font-size: .875rem;
	color: #8597a3;
	a {
		display: block;
		color: #6658d3;
		text-decoration: none;
	}
}

.success-message{
	background: linear-gradient(135deg, #f0f4f8 0%, #ffffff 100%);
    padding: 4rem;
    border-radius: 16px;
    font-size: 1.8rem;
	color: #6658d3;
	font-weight: 600;
	text-align: center;
	box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
	border: 2px solid rgba(102, 88, 211, 0.2);
}

@media only screen and (max-width: 959px),
only screen and (min-width: 768px) and (max-width: 959px),
only screen and (max-width: 767px),
only screen and (max-width: 479px) {
	

	.card-image {
		.card-heading {
			font-size: 1.50rem !important;
			color: #735400 !important;
			font-weight: 700 !important;
			text-shadow: 0 1px 2px rgba(255, 255, 255, 0.3) !important;

			small {
				font-weight: 300 !important;
				font-size: 1.4rem !important;
			}
		}
	}
}